import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DatapassService } from 'src/app/services/datapass.service';

@Component({
  selector: 'app-member2',
  templateUrl: './member2.component.html',
  styleUrls: ['./member2.component.css']
})
export class Member2Component {
  indexCar!: number;
  brand!: string;
  model!: string;
  year!: number;
  price!: number;


  constructor(private paramUrl: ActivatedRoute, public body: DatapassService) {
    this.indexCar = this.paramUrl.snapshot.params['indexCar'];
    console.log(this.body.cars);
  }

  pushCar() {
    let newCar = {
      brand: this.brand,
      model: this.model,
      year: this.year,
      price: this.price
    }
    this.body.cars.push(newCar);
  }

  unshiftCar() {
    let newCar = {
      brand: this.brand,
      model: this.model,
      year: this.year,
      price: this.price
    }
    this.body.cars.unshift(newCar);
  }

  removeCar(i: number) {
    this.body.cars.splice(i, 1);
  }
}
