import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './components/login/login.component';
import { Member1Component } from './components/member1/member1.component';
import { Member2Component } from './components/member2/member2.component';
import { WebapiComponent } from './components/webapi/webapi.component';
import { SqaureFlexComponent } from './components/sqaure-flex/sqaure-flex.component';
import { BuffetCalculatedComponent } from './components/buffet-calculated/buffet-calculated.component';
import { InputOutputComponent } from './pages/input-output/input-output.component';
import { TwoWayBindingComponent } from './pages/two-way-binding/two-way-binding.component';
import { CardSlideComponent } from './components/card-slide/card-slide.component';
import { EditApiComponent } from './pages/edit-api/edit-api.component';

const routes: Routes = [
  { path: '', component: LoginComponent },
  { path: 'login', component: LoginComponent },
  { path: 'member1', component: Member1Component },
  { path: 'member2', component: Member2Component },
  { path: 'member2/:indexCar', component: Member2Component },
  { path: 'webapi', component: WebapiComponent },
  { path: 'buffet-calculated', component: BuffetCalculatedComponent },
  { path: 'style-binding', component: SqaureFlexComponent },
  { path: 'input-output', component: InputOutputComponent },
  { path: 'two-way-binding', component: TwoWayBindingComponent },
  { path: 'card-slider', component: CardSlideComponent },
  { path: 'EditApi', component: EditApiComponent },
  { path: 'EditApi/:lineApi', component: EditApiComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
