import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-webapi',
  templateUrl: './webapi.component.html',
  styleUrls: ['./webapi.component.css']
})
export class WebapiComponent {
  request: any;
  names: any;
  freeAPI: string = 'https://www.anapioficeandfire.com/api/books/1';
  myapi: any;

  constructor(private http: HttpClient) {}

  listUserObser() {
    console.log('Process 1');
    let request = this.http.get('https://pokeapi.co/api/v2/version/7')
      .subscribe(response => {
        this.request = response;
        this.names = this.request.names;
        console.log('Process 2', this.names);
      }, error => {
        console.log('Process 2', error);
      });
    console.log('Process 3');
    // request.unsubscribe();
    console.log('Process 4');
  }

  async listUserAwait() {
    console.log('Process 1');
    let request = await this.http.get('https://pokeapi.co/api/v2/version/6').toPromise();
    this.request = request;
    this.names = this.request.names;
    console.log('Process 2', this.names);
    console.log('Process 3');
    console.log('Process 4');
  }

  async myApi() {
    this.myapi = await this.http.get('http://147.50.231.83:3000/api/product').toPromise();
    console.log(this.myapi);
  }
}
