import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { DatapassService } from 'src/app/services/datapass.service';

@Component({
  selector: 'app-member1',
  templateUrl: './member1.component.html',
  styleUrls: ['./member1.component.css']
})
export class Member1Component {
  indexCar!: string;

  constructor(private router: Router, private body: DatapassService) {}

  gotoMember2() {
    this.body.cars = [
      { brand: 'Toyota', model: 'viso', year: 2012, price: 260000 },
      { brand: 'Honda', model: 'jazz', year: 2017, price: 600000 },
      { brand: 'Ford', model: 'everest', year: 2010, price: 1600000 },
      { brand: 'Mitsubishi', model: 'stradra', year: 2009, price: 500000 },
      { brand: 'Mazda', model: 'Mazda 2', year: 2019, price: 585000 },
      { brand: 'BMW', model: 'series 5', year: 2019, price: 2485000 },
    ];
    this.router.navigateByUrl(`/member2/${this.indexCar}`);
  }
}
