import { Component } from '@angular/core';

@Component({
  selector: 'app-buffet-calculated',
  templateUrl: './buffet-calculated.component.html',
  styleUrls: ['./buffet-calculated.component.css']
})
export class BuffetCalculatedComponent {
  resultPrice = 0;

  calBuffer(value: string) {
    console.log(value);
    let price = Number(value);
    this.resultPrice = 3 * price / 4;
  }
}
